import React, { Component } from 'react'
import { Provider } from 'react-redux';
import { calculateResponsiveState } from 'redux-responsive'

import ThemeProvider from './src/components/ThemeProvider';
import creatStore from './src/stores/createStore';
import withResponsive from './src/hoc/withResponsive'

const store = creatStore();

class InitialDispatch extends Component {
  componentDidMount() {
    if (typeof window !== 'undefined') {
      store.dispatch(calculateResponsiveState(window))
    }
  }

  componentDidUpdate(prevProps) {
    if (!this.inited && prevProps.isMobile === this.props.isMobile) {
      this.forceUpdate()
    }
    this.inited = true
  }

  render() {
    return this.props.children
  }
}

const HocInitialDispatch = withResponsive(InitialDispatch)

export default ({ element }) => (
  <Provider store={store}>
    <HocInitialDispatch>
      <ThemeProvider>{element}</ThemeProvider>
    </HocInitialDispatch>
  </Provider>
)
